.footer_container {
	margin-top: 5rem;
	padding: 0 2rem;
	width: 100%;
}

.footer_container > hr {
	margin-top: 1rem;
	width: 100%;
	height: 1px;
	border: none;
	background: #fff;
}

.footer {
	display: flex;
	justify-content: space-around;
	width: 100%;
}

.footer > :nth-child(1) {
	margin-right: 2rem;
}

.information {
	flex: 30%;
	display: flex;
	flex-direction: column;
	gap: 1rem;
	max-width: 12rem;
}
.contact {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	font-size: 0.7rem;
	width: inherit;
}

.contact > span:nth-of-type(1) {
	font-weight: 500;
	font-size: 1.2rem;
}

.pngLine {
	display: flex;
	gap: 1rem;
	align-items: center;
}

.pngLine:hover {
	cursor: pointer;
}
.pngLine > span {
	width: 70%;
}

.icon {
	width: 25px;
}

.footer_bottom {
	margin-top: 2rem;

	display: flex;
	flex-direction: column;
	width: 100%;
	text-align: center;
	font-size: 0.65rem;
}

@media screen and (max-width: 640px) {
	.footer {
		justify-content: space-between;
		flex-wrap: wrap;
		font-size: 0.7rem;
	}

	.footer > :nth-child(1) {
		display: none;
	}

	.information:not(:last-child) {
		margin-bottom: 2rem;
	}
}
